/* eslint-disable max-len */
import React, {
  lazy, Suspense, useEffect, useState,
} from 'react'
import { Route, BrowserRouter as Router, Routes } from 'react-router-dom'
import axios from 'axios'
import { useDispatch, useSelector } from 'react-redux'

import './App.css'

import Develop from './pages/develop/index'

import AuthMiddleware from './middleware/AuthMiddleware'
import OnlyPublicMiddleware from './middleware/OnlyPublicMiddleware'
import Profil from './pages/profil'
import Create from './pages/create'
import Slug from './pages/_slug'
import PrintoutSlug from './pages/printout/_slug'
import { setUser } from './store/auth'

/*
  Lazy load
*/
const Index = lazy(() => import('./pages/index'))

const Login = lazy(() => import('./pages/login/index'))

function App() {
  const dispatch = useDispatch()
  const [loading, setLoading] = useState(true)
  const [isGetUserInfo, setIsGetUserInfo] = useState(false)

  const dummy = useSelector((state) => state.auth.dummy)
  // cek get user dari api
  function getUserInfo() {
    setIsGetUserInfo(true)
    if (!dummy) {
      if (localStorage.token) {
        axios.defaults.headers.common = { Authorization: `Bearer ${localStorage.token.substring(0, localStorage.token.length - 1)}` }
      }
      axios.get(`${axios.defaults.baseURL}/api/user`, {
        params: {
          appName: 'ramp',
        },
      }).then((res) => {
        setLoading(false)
        if (res.data.status) {
          dispatch(setUser(res.data.data))
        } else {
          console.log(res)
        }
      })
        .catch(() => {
          setLoading(false)
        })
    } else {
      setLoading(false)
    }
  }
  if (!isGetUserInfo) {
    getUserInfo()
  }

  useEffect(() => {
    if (localStorage.theme) {
      const root = window.document.documentElement
      root.classList.remove('dark', 'light')
      root.classList.add(localStorage.theme)

      localStorage.setItem('theme', localStorage.theme)
    } else {
      localStorage.setItem('theme', 'light')
    }
  })

  return (
    <div>
      {
        (!loading) ? (
          <Router>
            <Routes>
              <Route
                path="/"
                exact
                element={(
                  <AuthMiddleware abilities="['inspector', 'pilot']">
                    <Suspense fallback={<div>Loading... </div>}>
                      <Index />
                    </Suspense>
                  </AuthMiddleware>
                )}
              />
              <Route
                path="/create"
                exact
                element={(
                  <AuthMiddleware abilities="['inspector']">
                    <Suspense fallback={<div>Loading... </div>}>
                      <Create />
                    </Suspense>
                  </AuthMiddleware>
                )}
              />
              <Route
                path="/profil"
                exact
                element={(
                  <AuthMiddleware abilities="['inspector', 'pilot']">
                    <Suspense fallback={<div>Loading... </div>}>
                      <Profil />
                    </Suspense>
                  </AuthMiddleware>
                )}
              />
              <Route
                path="/:slug"
                exact
                element={(
                  <AuthMiddleware abilities="['inspector', 'pilot']">
                    <Suspense fallback={<div>Loading... </div>}>
                      <Slug />
                    </Suspense>
                  </AuthMiddleware>
                )}
              />
              <Route
                path="/printout/:slug"
                exact
                element={(
                  <AuthMiddleware abilities="['inspector', 'pilot']">
                    <Suspense fallback={<div>Loading... </div>}>
                      <PrintoutSlug />
                    </Suspense>
                  </AuthMiddleware>
                )}
              />
              <Route
                path="/login"
                element={(
                  <OnlyPublicMiddleware>
                    <Suspense fallback={<div>Loading... </div>}>
                      <Login />
                    </Suspense>
                  </OnlyPublicMiddleware>
                )}
              />

              <Route
                path="/develop"
                element={<Develop />}
              />
            </Routes>
          </Router>
        ) : 'loading'
      }
    </div>
  )
}

export default App
