  function handleSubmit(e) {
    e.preventDefault()
    const formattedForm = {}
    Object.keys(form).forEach((f) => {
      formattedForm[f] = form[f]
    })
    // cek yg checkbox (type operation, action taken)
    const typeOfOperationArr = []
    const actionTakenArr = []
    Object.keys(form).forEach((f) => {
      // jika includes dan bernilai true
      if (f.includes('type_of_operation_') && form[f]) {
        typeOfOperationArr.push(f.substring('type_of_operation_'.length))
      }
      if (f.includes('action_taken_') && form[f]) {
        actionTakenArr.push(f.substring('action_taken_'.length))
      }
    })
    formattedForm.type_of_operation = JSON.stringify(typeOfOperationArr).replace('[', '').replace(']', '').replace(/"/g, '')
    formattedForm.action_taken = JSON.stringify(actionTakenArr).replace('[', '').replace(']', '').replace(/"/g, '')

    // format form cehcklist
    const formattedChecklist = []
    mItem.forEach((f) => {
      // check apakah item itu ada dikirim apa tidak
      const hasItem = formChecklist[`${f.id_ramp_item}_check`] || formChecklist[`${f.id_ramp_item}_remark`]
      if (hasItem) {
        formattedChecklist.push({
          id_ramp_item: f.id_ramp_item,
          check: (formChecklist[`${f.id_ramp_item}_check`]) ? 1 : 0,
          remark: (formChecklist[`${f.id_ramp_item}_remark`]) ? 1 : 0,
          cat: formChecklist[`${f.id_ramp_item}_cat`] || '',
          comment: formChecklist[`${f.id_ramp_item}_comment`] || '',
        })
      }
    })
    getForm({
      tabMenuActive,
      formattedForm,
      formattedChecklist,
    })
    // setForm({})
    // setFormChecklist({})
    // setFormChecklistKet([])
    // console.log(form)
  }

