import { createSlice } from '@reduxjs/toolkit'

export const rampChecklist = createSlice({
  name: 'rampChecklist',
  initialState: {
    rampChecklist: [
      {
        id_ramp_checklist: 1,
        id_ramp: 1,
        id_ramp_item: 2,
        check: 1,
        remark: 1,
        cat: '1',
        comment: 'oke',
        dummy: true,
      },
    ],
  },
  reducers: {
    setRampChecklist: (state, val) => {
      state.rampChecklist = val.payload
    },
  },
})

export const { setRampChecklist } = rampChecklist.actions

export default rampChecklist.reducer
