import React from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify'
import { ArrowLeftIcon } from '@heroicons/react/outline'
import { useSelector } from 'react-redux'
import axios from 'axios'
import InspectionForm from '../../components/inspection/InspectionForm'
import UserTemplate from '../../template/UserTemplate'
import 'react-toastify/dist/ReactToastify.css'

function Create() {
  const dummy = useSelector((state) => state.auth.dummy)
  const redirectAfterSave = true
  const routerNavigate = useNavigate()

  const getForm = (form) => {
    if (!dummy) {
      const generalForm = form.formattedForm
      const formData = new FormData()
      const keyForm = ['date', 'time', 'place', 'operator', 'state', 'aoc_no', 'route_from',
        'flight_no_to', 'route_to', 'flight_no_from', 'type_of_operation', 'aircraft_model',
        'regis_mark', 'serial_number', 'licensing', 'ack_name', 'ack_function', 'action_taken']
      keyForm.forEach((e) => {
        formData.append(e, generalForm[e])
      })
      if (form.type_of_operation === '') {
        toast.error('Select Type of Operation', {
          hideProgressBar: true,
        })
        return false
      }
      if ('ack_signature_file' in generalForm) {
        formData.append('ack_signature', generalForm.ack_signature_file, 'image.png')
      }

      axios.post(`${axios.defaults.baseURL}/api/ramp`, formData).then((res) => {
        if (res.data.status) {
          toast.success(res.data.message, {
            hideProgressBar: true,
          })
          if (redirectAfterSave) {
            setTimeout(() => {
              routerNavigate(`/${res.data.data.slug}`)
            }, 1000)
          }
        } else {
          toast.error(res.data.message, {
            hideProgressBar: true,
          })
        }
      })
        .catch(() => {
          toast.error('Ada kesalahan teknis, harap coba lagi nanti.', {
            hideProgressBar: true,
          })
        })
    } else {
      console.log(form)
      toast.success('success', {
        hideProgressBar: true,
      })
      if (redirectAfterSave) {
        setTimeout(() => {
          routerNavigate('/?from=tambah')
        }, 1000)
      }
    }
    return true
  }

  return (
    <UserTemplate showBottomNav={false}>
      <div className="px-4 py-8">
        <Link
          to="/"
          className="dark:text-gray-300"
        >
          <ArrowLeftIcon className="w-6 h-6" />
        </Link>
        <div className="mt-4">
          <InspectionForm getForm={getForm} />
        </div>
        <ToastContainer />
      </div>
    </UserTemplate>
  )
}

export default Create
