import { createSlice } from '@reduxjs/toolkit'

export const rampInspector = createSlice({
  name: 'rampInspector',
  initialState: {
    rampInspector: [
      {
        id_ramp_inspector: 1,
        id_ramp: 1,
        name: 'ASMAUL',
        signature: '',
      },
      {
        id_ramp_inspector: 2,
        id_ramp: 1,
        name: 'ARWIRO',
        signature: '',
      },
    ],
  },
  reducers: {
    setRampInspector: (state, val) => {
      state.rampInspector = val.payload
    },
  },
})

export const { setRampInspector } = rampInspector.actions

export default rampInspector.reducer
