import { configureStore } from '@reduxjs/toolkit'
import authReducer from './auth'
import counterReducer from './counter/counterSlice' // ini hanya example
import rampInspectionReducer from './ramp-inspection'
import mainReducer from './main'
import rampChecklistReducer from './ramp-checklist'
import rampInspectorReducer from './ramp-inspector'

export default configureStore({
  reducer: {
    auth: authReducer,
    rampInspection: rampInspectionReducer,
    rampChecklist: rampChecklistReducer,
    rampInspector: rampInspectorReducer,
    counter: counterReducer,
    main: mainReducer,
  },
})
