/* eslint-disable camelcase */
import { createSlice } from '@reduxjs/toolkit'
// import axios from 'axios'

export const auth = createSlice({
  name: 'auth',
  initialState: {
    dummy: false,
    // authMode: 'api',
    authMode: 'sanctum',
    user: null,
    role: [],
    currentRole: null,
  },
  reducers: {
    setUser: (state, val) => {
      const { user, role, current_role } = val.payload
      state.user = user
      state.role = role
      state.currentRole = current_role
    },
    setDummyUser: (state) => {
      state.user = {
        id: 1,
        name: 'Agus Putra Witama',
        email: 'putrawitama@gmail.com',
      }
      state.role = [
        {
          id_role: 1,
          nama_role: 'Admin',
          abilities: 'ramp,inspector',
        },
        {
          id_role: 2,
          nama_role: 'User',
          abilities: 'ramp,inspector',
        },
      ]
      state.currentRole = {
        id_role: 2,
        nama_role: 'User',
        abilities: 'ramp,inspector',
      }
    },
  },
})

export const { setUser, setDummyUser } = auth.actions

// api list
// export const sanctum = () => axios.get('http://127.0.0.1:8000/sanctum/csrf-cookie')
// export const login = (data) => axios.post('http://127.0.0.1:8000/api/login', data)

export default auth.reducer
