/* eslint-disable camelcase */
import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useParams } from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify'
import { ArrowLeftIcon } from '@heroicons/react/outline'
import axios from 'axios'
import InspectionForm from '../../components/inspection/InspectionForm'
import UserTemplate from '../../template/UserTemplate'
import 'react-toastify/dist/ReactToastify.css'
import { setRampInspectionShow } from '../../store/ramp-inspection'
import { setRampChecklist } from '../../store/ramp-checklist'
import { setRampInspector } from '../../store/ramp-inspector'
import ButtonComp from '../../components/global/ButtonComp'

function Slug() {
  const dispatch = useDispatch()
  // const routerNavigate = useNavigate()
  const dummy = useSelector((state) => state.auth.dummy)
  const currentRole = useSelector((state) => state.auth.currentRole)
  // flag jika sudah load
  const [isLoadRampInspection, setIsLoadRampInspection] = useState(false)
  // indikator loading
  const [isLoading, setIsLoading] = useState(true)

  const { slug } = useParams()

  const inspectionShow = useSelector((state) => state.rampInspection.rampInspectionShow)
  const checklistShow = useSelector((state) => state.rampChecklist.rampChecklist)
  const inspectorShow = useSelector((state) => state.rampInspector.rampInspector)

  // eslint-disable-next-line radix
  // let inspectionShow = null
  // if (!dummy) {
  //   inspectionShow = useSelector((state) => state.rampInspection.rampInspectionShow)
  // } else {
  //   inspectionShow = inspection.find((e) => e.slug === slug)
  // }

  // console.log(inspectionShow)

  const getForm = (form) => {
    if (!dummy) {
      const { tabMenuActive, formattedForm, formattedChecklist } = form
      if (tabMenuActive === 'General') {
        // update form general, ramp
        // fungsi ini mirip dengan create
        const generalForm = formattedForm
        const formData = new FormData()
        formData.append('_method', 'PUT')
        const keyForm = ['date', 'time', 'place', 'operator', 'state', 'aoc_no', 'route_from',
          'flight_no_to', 'route_to', 'flight_no_from', 'type_of_operation', 'aircraft_model',
          'regis_mark', 'serial_number', 'licensing', 'ack_name', 'ack_function']
        keyForm.forEach((e) => {
          formData.append(e, generalForm[e])
        })
        if (generalForm.type_of_operation === '') {
          toast.error('Select Type of Operation', {
            hideProgressBar: true,
          })
          return false
        }
        if ('ack_signature_file' in generalForm) {
          formData.append('ack_signature', generalForm.ack_signature_file, 'image.png')
        }

        axios.post(`${axios.defaults.baseURL}/api/ramp/${slug}`, formData).then((res) => {
          if (res.data.status) {
            toast.success(res.data.message, {
              hideProgressBar: true,
            })
            if (slug !== res.data.data.slug) {
              setTimeout(() => {
                // routerNavigate(`/${res.data.data.slug}`)
                window.location = `/${res.data.data.slug}`
              }, 1000)
            }
          } else {
            toast.error(res.data.message, {
              hideProgressBar: true,
            })
          }
        })
          .catch(() => {
            toast.error('Ada kesalahan teknis, harap coba lagi nanti.', {
              hideProgressBar: true,
            })
          })
      } else if (tabMenuActive === 'Checklist') {
        // update checklist by slug ini
        if (formattedChecklist.length === 0) {
          toast.error('Check At Least One Item', {
            hideProgressBar: true,
          })
          return false
        }
        if (formattedForm.action_taken === '') {
          toast.error('Select Action Taken', {
            hideProgressBar: true,
          })
          return false
        }

        const formData = new FormData()
        formData.append('checklist', JSON.stringify(formattedChecklist))
        formData.append('action_taken', formattedForm.action_taken)

        axios.post(`${axios.defaults.baseURL}/api/ramp/update-checklist/${slug}`, formData).then((res) => {
          if (res.data.status) {
            toast.success(res.data.message, {
              hideProgressBar: true,
            })
            if (slug !== res.data.data.slug) {
              setTimeout(() => {
                // routerNavigate(`/${res.data.data.slug}`)
                window.location = `/${res.data.data.slug}`
              }, 1000)
            }
          } else {
            toast.error(res.data.message, {
              hideProgressBar: true,
            })
          }
        })
          .catch(() => {
            toast.error('Ada kesalahan teknis, harap coba lagi nanti.', {
              hideProgressBar: true,
            })
          })
      } else if (tabMenuActive === 'Inspector(s) Signature') {
        if ('crew_comment' in formattedForm) {
          const formData = new FormData()
          formData.append('crew_comment', formattedForm.crew_comment)
          axios.post(`${axios.defaults.baseURL}/api/ramp/update-crew-comment/${slug}`, formData).then((res) => {
            if (res.data.status) {
              toast.success(res.data.message, {
                hideProgressBar: true,
              })
              if (slug !== res.data.data.slug) {
                setTimeout(() => {
                  // routerNavigate(`/${res.data.data.slug}`)
                  window.location = `/${res.data.data.slug}`
                }, 1000)
              }
            } else {
              toast.error(res.data.message, {
                hideProgressBar: true,
              })
            }
          })
            .catch(() => {
              toast.error('Ada kesalahan teknis, harap coba lagi nanti.', {
                hideProgressBar: true,
              })
            })
        } else {
          toast.success('No data', {
            hideProgressBar: true,
          })
        }
      }
    } else {
      console.log(form)
      toast.success('Berhasil mengedit data', {
        hideProgressBar: true,
      })
    }
    return true
  }

  const getRampInspector = () => {
    axios.get(`${axios.defaults.baseURL}/api/ramp-inspector`, {
      params: {
        slug,
      },
    })
      .then((res) => {
        if (res.data.status) {
          dispatch(setRampInspector(res.data.data))
        } else {
          //
        }
      })
      .catch(() => {
        //
      })
  }

  const getFormInspector = (form) => {
    const { aksiInspectorSignature, formInspector, signature_file } = form

    if (!('name' in formInspector)) {
      toast.error('Type Inspectors Name', {
        hideProgressBar: true,
      })
      return false
    }

    const formData = new FormData()
    formData.append('slug', slug)
    formData.append('name', formInspector.name)

    let axiosAction = null

    if (aksiInspectorSignature === 'create') {
      formData.append('signature', signature_file, 'image.png')
      axiosAction = axios.post(`${axios.defaults.baseURL}/api/ramp-inspector`, formData)
    } else if (aksiInspectorSignature === 'edit') {
      formData.append('signature', signature_file, 'image.png')
      formData.append('_method', 'PUT')
      axiosAction = axios.post(`${axios.defaults.baseURL}/api/ramp-inspector/${formInspector.id_ramp_inspector}`, formData)
    } else if (aksiInspectorSignature === 'delete') {
      axiosAction = axios.delete(`${axios.defaults.baseURL}/api/ramp-inspector/${formInspector.id_ramp_inspector}`)
    }
    axiosAction.then((res) => {
      if (res.data.status) {
        getRampInspector()
      } else {
        //
      }
    })
      .catch(() => {
        //
      })
    return true
  }
  // eslint-disable-next-line no-unused-vars
  const showRamp = () => {
    setIsLoading(true)
    axios.get(`${axios.defaults.baseURL}/api/ramp/${slug}`, {
      params: {
        withChecklist: 1,
        withInspector: 1,
      },
    }).then((res) => {
      setIsLoading(false)
      setIsLoadRampInspection(true)
      if (res.data.status) {
        dispatch(setRampInspectionShow(res.data.data))
        dispatch(setRampChecklist(res.data.data.ramp_checklist))
        dispatch(setRampInspector(res.data.data.ramp_inspector))
      } else {
        //
      }
    })
      .catch((err) => {
        setIsLoading(false)
        setIsLoadRampInspection(true)
        if (err.response && err.response.status && err.response.status === 404) {
          window.location = '/'
        }
      })
  }

  const deleteRampInspection = () => {
    axios.delete(`${axios.defaults.baseURL}/api/ramp/${slug}`).then((res) => {
      if (res.data.status) {
        toast.success(res.data.message, {
          hideProgressBar: true,
        })
        setTimeout(() => {
          window.location = '/'
        }, 1000)
      } else {
        toast.error(res.data.message, {
          hideProgressBar: true,
        })
      }
    })
      .catch(() => {
        toast.error('An error occured', {
          hideProgressBar: true,
        })
      })
  }

  const downloadPdf = () => {
    axios.get(`${axios.defaults.baseURL}/api/ramp/export-pdf/${slug}`, {
      responseType: 'arraybuffer',
    // eslint-disable-next-line no-unused-vars
    }).then((res) => {
      const a = document.createElement('a')
      document.body.appendChild(a)
      a.style = 'display: none'

      const blob = new Blob([res.data], { type: 'application/pdf' })
      const url = window.URL.createObjectURL(blob)

      a.href = url
      const fileName = `${inspectionShow.regis_mark} ${inspectionShow.date} ${inspectionShow.time}.pdf`
      a.download = fileName
      a.click()
      window.URL.revokeObjectURL(url)
      // window.open(url)
    })
      .catch(() => {
        toast.error('An error occured', {
          hideProgressBar: true,
        })
      })
  }

  useEffect(() => {
    if (!dummy && !isLoadRampInspection) {
      // dispatch(setRampInspectionShow({}))
      showRamp()
    }
  })

  return (
    <UserTemplate showBottomNav={false}>
      <div className="px-4 py-8">
        <div className="flex">
          <Link
            to="/"
            className="mr-3 dark:text-gray-300"
          >
            <ArrowLeftIcon className="w-6 h-6" />
          </Link>
          {
            (!isLoading) ? (
              <div className="ml-auto">
                <ButtonComp
                  btnColor="red"
                  btnText="download as pdf"
                  btnIcon="DocumentDownload"
                  handleClick={downloadPdf}
                />
              </div>
            ) : ''
          }

        </div>
        <div className="mt-4">
          {
            (!isLoading) ? (
              <InspectionForm
                isReadOnly={(!currentRole.role_slug === 'inspector')}
                inspection={inspectionShow}
                checklist={checklistShow}
                inspector={inspectorShow}
                getForm={getForm}
                getFormInspector={getFormInspector}
                deleteRampInspection={deleteRampInspection}
              />
            ) : (
              <div className="dark:text-gray-300 text-sm text-center">
                Loading...
              </div>
            )
          }

        </div>
        <ToastContainer />
      </div>
    </UserTemplate>
  )
}

export default Slug
