import axios from 'axios'
import React from 'react'
import { useSelector } from 'react-redux'
import DarkModeToggler from '../../components/global/DarkModeToggler'
import UserTemplate from '../../template/UserTemplate'

function Profil() {
  const user = useSelector((state) => state.auth.user)
  const authMode = useSelector((state) => state.auth.authMode)
  async function logout() {
    if (authMode === 'sanctum') {
      await axios.post(`${axios.defaults.baseURL}/logout`)
    } else if (authMode === 'api') {
      await axios.post(`${axios.defaults.baseURL}/api/logout`)
    }
    setTimeout(() => {
      window.location = '/'
    }, 2000)
  }
  return (
    <UserTemplate>
      <div className="px-4 py-8">
        <div className="text-3xl text-gray-700 dark:text-gray-300 text-center">
          <h1>Profil</h1>
        </div>
        <div className="grid grid-cols-5 mt-8">
          <div className="rounded-full aspect-square
          w-full border-2 border-brand-600"
          />
          <div className="col-span-4 p-4">
            <h3 className="font-medium dark:text-gray-300">
              {user.name}
            </h3>
            <p className="text-gray-600 dark:text-gray-500">
              {user.email}
            </p>
          </div>
        </div>

        <div className="border dark:border-gray-700 rounded-lg bg-white dark:bg-gray-800 mt-8 text-sm">
          {/* <div className="border-b dark:border-gray-700 p-3 dark:text-gray-300">
            Edit Profil
          </div> */}
          <div className="border-b dark:border-gray-700 p-3 dark:text-gray-300">
            <DarkModeToggler />
          </div>
          <button
            type="button"
            className="dark:border-gray-700 p-3  w-full text-red-600 text-left"
            onClick={logout}
          >
            Logout
          </button>
        </div>
      </div>
    </UserTemplate>
  )
}

export default Profil
